<template lang="pug">
div.content
  .descr
    h2 {{name}}
    p Форма отправки данных. В качестве хранилища используется #[a(href="https://firebase.google.com/") Firebase]
  .app
    .contact
      h3 Contact me
      form#contactForm(v-on:submit.prevent='addContact')
        .contact__flex
          p
            label Name
            input#name(type='text', name='name', v-model='newContact.name', required='')
          p
            label Company
            input#company(type='text', name='company', v-model='newContact.company')
        .contact__flex
          p
            label Email Address
            input#email(type='email', name='email', v-model='newContact.mail', required='')
          p
            label Phone Number
            input#phone(type='text', name='phone', v-model='newContact.phone')
        button.btn.btn_absolute(type='submit') Submit
      table
        transition(name="slide-fade")
          thead(v-if="contacts.length !== 0")
            th Name
            th Company
            th Mail
            th Phone
            th Delete
        transition-group(tag="tbody" name="slide-fade")
          tr.result(v-for='contact in contacts' :key="contact['.key']")
            td {{contact.name}}
            td {{contact.company}}
            td {{contact.mail}}
            td {{contact.phone}}
            td
              button.btn(aria-hidden='true', v-on:click='removeContact(contact)') delete
</template>

<script>
import Firebase from 'firebase'
let config = {
  apiKey: "AIzaSyDpTqtTdjibnXxitLwDYiFR8P00zWAOzBI",
   authDomain: "vue01-7a33b.firebaseapp.com",
   databaseURL: "http://vue01-7a33b.firebaseio.com",
   projectId: "vue01-7a33b",
   storageBucket: "vue01-7a33b.appspot.com",
   messagingSenderId: "544189631905"
 };

let app = Firebase.initializeApp(config);
let db = app.database();
let contactsRef = db.ref('contacts');


export default {
  name: 'form',
  firebase: {
    contacts: contactsRef
  },
  data () {
   return {
     name: "Форма",
     newContact: {
         name: '',
         company: '',
         mail: '',
         phone: '',
     }
   }
 },
 methods: {
     addContact: function () {
       contactsRef.push(this.newContact);
       this.newContact.name = '';
       this.newContact.company = '';
       this.newContact.mail = '';
       this.newContact.phone = '';
       this.$toast('Сообщение отправлено.', {
         transition: 'slide-down',
       })
     },
     removeContact: function (contact) {
       contactsRef.child(contact['.key']).remove()
     }
   },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

h3 {
  margin: 0;
}

.content {
  position: relative;
}

#contactForm {
  width: 100%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  margin-bottom: 20px;
}

.contact__flex {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  flex: 1;
  margin: 20px;
  justify-content:flex-start;
}

.contact__flex label {
  margin-bottom: 15px;
  margin-top: 10px;
}

.contact__flex p {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  margin: 0;
}

.contact__flex input {
  border-radius: 50px;
  padding: 5px 10px;
  border: 1px solid #ccc;
}

.contact__flex input:focus {
  outline: none;
}


.contact__flex .btn {
  width: 80px;
  margin: 0 auto;
  margin-top: 5px;
}

.btn_absolute {
  position: absolute;
  top: -15px;
  right: -10px;
  border-radius: 50px;
  width: 65px;
  height: 65px;
  font-weight: bold;
}


table {
  width: 100%;
}
.result {
  margin: 0 auto;
  margin-bottom: 5px;
}

th {
  background-color: #ccc;
  color: #fff;
  background-color: #41B883;
}

th {
  padding-top: 10px;
  padding-bottom: 10px;
}

tbody {
  background-color: #fff;
}

td {
  padding-bottom: 10px;
  padding-top: 10px;
}

</style>



