<template lang="pug">
.content
	.descr
		h2 {{name}}
		p {{descr}}
	.app
		.Chart__list
			.Chart
				h2 Linechart
				line-example
</template>


<script>

import LineExample from './LineChart.js'

export default {
  name: 'diagram',
	components: {
    LineExample
  },
  data: function() {
    return {
      name: "Диаграмма",
      descr: "Duis aute irure dolor in reprehenderit.",
      error: false,
      succes: false,
			peoples: ['ken', 'park', 'mean']
    }
  },
}
</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

</style>



