<template lang="pug">
div.content
  .descr
    h2 {{name}}
    p Фотогалерея с пагинацией. Используется API сервиса #[a(href="http://unsplash.com") unsplash.com]
  .app
    .wrap
      transition(name="fade" mode="out-in")
        .photos
          .photo(v-for="photo in photos")
            img(v-bind:src="photo.urls.small" alt="img")
            .photo__media
              img(:src="photo.user.profile_image.small" alt="img")
              a.photo__link( :class="{ photo__link_active : photo.user.portfolio_url}" :href="photo.user.portfolio_url") {{photo.user.name}}
      .pagination
        .pagination__left
          button(v-on:click="prevPage" v-show="hasPrev()") Prev
        .curent-page
          span {{currentPage}} из {{totalPage}}
        .pagination__right
          button(v-on:click="nextPage" v-show="hasNext()") Next
</template>

<script>

let appId = "6076e83c69e631025bded94e3c1e028f02a25e05a718f31bba89863451d4afe7"


export default {
  name: 'foto',
  data: function() {
    return {
      name: "Фотогалерея",
      photos: [],
      totalPhotos: 0,
      perPage: 9,
      currentPage: 1,
      pageRange: 2,
    }
  },
  methods: {
    fetchPhotos(page) {
      let options = {
        params: {
          client_id: appId,
          page: page,
          per_page: this.perPage
        }
      }
      this.$http.get("https://api.unsplash.com/photos", options)
      .then(response => {
        this.photos = response.data
        this.totalPhotos = parseInt(response.headers.get('x-total'))
        this.currentPage = page
      },console.log)
    },
    nextPage: function(){
      this.fetchPhotos(this.currentPage + 1);
    },
    prevPage: function(){
      this.fetchPhotos(this.currentPage - 1);
    },
    hasPrev() {
      return this.currentPage > 1
    },
    hasNext() {
      return this.currentPage < this.totalPage
    }
  },
  computed: {
    totalPage: function(){
      return Math.ceil(this.totalPhotos/this.perPage)
    }
  },
  created() {
    //do something after creating vue instance
    this.fetchPhotos(this.currentPage);
  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="css" scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.content {
  height: 100%;
  width: 100%;
}
.wrap {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;

}
.photos {
  width: 100%;
  height: 100%;
  height: 800px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  clear: both;
}

.photo {
  position: relative;
  width: 33%;
  height: 32%;
  margin-bottom: 3px;
  box-shadow: 0 1px 2px rgba(0,0,0, 0.2);
}

.photo img {
  width: 100%;
  /*height: 18vh;*/
  height: 100%;
  /*height: 220px;*/
  object-fit:cover;
}

.photo__media {
  position: absolute;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 5px 0;
  background-color: #fff;
}
.photo__media img {
  width: 10%;
  height: 10%;
  border-radius: 20px;
  margin-right: 10px;
}

.photo__media a {
  color: #35495E;
}

.pagination {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pagination__left, .pagination__right  {
  min-width: 50px;
}


.pagination__left button, .pagination__right button {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 50px;
  padding: 5px 10px;
  cursor: pointer;
}

.pagination__left button:hover, .pagination__right button:hover {
  color: #fff;
  background-color: #41B883;
  transition: all 0.2s ease-in-out;
}

.pagination__left button:focus, .pagination__right button:focus {
  outline: none;
}

.pagination__left button:active, .pagination__right button:active {
  background-color: #22B883;
}

.photo__link.photo__link_active {
  color: #42b983;
}
</style>



