<template lang="pug">
div.content
  .descr
    h2 {{name}}
    p {{descr}}
  .app
    .todo
      .todo__header
        form(v-on:submit="addTask")
          input(type="text", placeholder="Add task"  v-model='tasks.name')
          input(type="submit", value='Add')
      .todo__body
        table
          transition(name="slide-fade")
            thead(v-if="tasks.length !== 0")
              th Done
              th Task Name
              th Delete Task
          transition-group(tag="tbody" name="slide-fade")
            tr(v-for="task in tasks" :key="task.name")
              td
                input(type="checkbox", v-model="task.done")
              td
                span(:class="{ taskDone: task.done }") {{ task.name }}
              td
                button.btn(v-on:click="deleteTask(task)") Delete
</template>

<script>
export default {
  name: 'todo',
  data: function() {
    return {
      name: "Список задач",
      descr: "Позволяет добавить, удалить задачу. Или пометить, как выполненную.",
      tasks: [{
          name: 'just do it',
          done: false
        },
        {
          name: 'keep calm and carry on',
          done: false
        },
      ]
    }
  },
  methods: {
    addTask: function(e) {
      e.preventDefault();
      this.tasks.push({
        name: this.tasks.name,
        done: false
      });
    },
    deleteTask: function(task) {
      this.tasks.splice(this.tasks.indexOf(task), 1)
    },
    doneTasks() {
      return this.tasks.done;
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.taskDone {
  text-decoration: line-through;
  color: #ccc;
}

.todo {
  height: 100%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.todo__header {
  margin-bottom: 20px;
}

.todo__header input {
  margin: 5px;
  border-radius: 50px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 50px;
  padding: 5px 10px;
}

.todo__header input:last-child {
  background-color: #ccc;
  color: #fff;
  background-color: #41B883;
  border: 1px solid #ccc;
  border-radius: 50px;
  padding: 5px 10px;
  cursor: pointer;
}


.todo__header input:focus {
  outline: none;
}

.todo__body {
  margin: 0 auto;
  width: 100%;
}

table {
  width: 100%;
}

thead {
  background-color: #ccc;
  color: #fff;
  background-color: #41B883;
}

th {
  padding-top: 10px;
  padding-bottom: 10px;
}

tbody {
  background-color: #fff;
}

td {
  padding-bottom: 10px;
  padding-top: 10px;
}
</style>



